//
// user.scss
// Use this to write your custom SCSS
//

body::after {
	position: absolute;
	width: 0;
	height: 0;
	overflow: hidden;
	z-index: -1; // hide images
	/*content:url(../../assets/img/cover/cover-dark-full.jpg) url(img2.png) url(img3.gif) url(img4.jpg);*/ // load images
	/*content:url(../../assets/img/cover/cover-dark-s.png) url(../../assets/img/cover/cover-s.png);*/
}

html section.hero,
html.dark-mode section.hero {
	background: url(../../assets/img/cover/cover-s.jpg) no-repeat center center fixed;
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
	height: 100vh;
}

html.dark-mode section.hero {
	background-image: url(../../assets/img/cover/cover-dark-s.jpg) !important;
}

html h1 {
	color: $light;
}

html.dark-mode h1,
html.dark-mode h2,
html.dark-mode h3,
html.dark-mode h4,
html.dark-mode h5,
html.dark-mode h6 {
	color: $light !important;
	text-shadow: 0 0 5px #000;
}

.dark-mode .btn-light,
.dark-mode .btn-video {
	background-color: #1a242e;
	color: #ffffff;
	border-color: rgba(255, 255, 255, 0.25);
}
.dark-mode .btn-light:hover {
	background-color: #ffffff;
	color: #3e4265;
}

.navbar-expand-lg .offcanvas .offcanvas-body {
	display: flex !important;
}

a.brands {
	text-decoration: none;
}

span.client,
span.solution {
	text-align: center;
	color: $gray-600;
	text-decoration: none;
}

span.client {
	font-weight: bold;
	color: $gray-700;
}

.dark-mode .card {
	background-color: #cccccc;
	color: rgba(255, 255, 255, 0.7);
}
.dark-mode .contact-cta .text-dark {
	color: $dark !important;
}

html.dark-mode .dark {
	-webkit-filter: brightness(0) contrast(0.8);
	filter: brightness(0) contrast(0.8);
}

html.dark-mode .light {
	-webkit-filter: brightness(0) invert(0.7);
	filter: brightness(0) invert(0.7);
}

html.dark-mode .bg-dark {
	background-color: rgb(35, 45, 54) !important;
}

.btn-primary {
	color: $dark !important;
	background-color: $primary;
	border-color: $primary;
	box-shadow: unset;
}

.ss-btn {
	display: inline-flex;
	align-items: center;
	justify-content: center;
}

nav[data-toggle='toc'] {
	top: 42px;
}

nav[data-toggle='toc'] .nav > li > a,
nav[data-toggle='toc'] .nav > li > a:focus,
nav[data-toggle='toc'] .nav > li > a:hover,
nav[data-toggle='toc'] .nav-link.active,
nav[data-toggle='toc'] .nav-link.active:focus,
nav[data-toggle='toc'] .nav-link.active:hover {
	color: var(--si-body-color);
	border: none;
	padding-left: 5px;
}

caption {
	background-color: #000;
	color: #ffffff;
}

/* Filter Content */

.filter {
	display: none; /* Hidden by default */
}
.show {
	display: block;
}
.selected {
	background-color: #666 !important;
	color: white !important;
	font-weight: normal !important;
}

span.wip {
	float: right;
	padding: 2px 5px;
	background-color: #ffb347;
	color: #000;
	font-size: 12px;
	font-style: italic;
	border-bottom-left-radius: 10px;
}

.rotate {
	transform-origin: left bottom 0;
	transform: rotate(-90deg);

	/* Legacy vendor prefixes that you probably don't need... */

	/* Safari */
	-webkit-transform: rotate(-90deg);

	/* Firefox */
	-moz-transform: rotate(-90deg);

	/* IE */
	-ms-transform: rotate(-90deg);

	/* Opera */
	-o-transform: rotate(-90deg);

	/* Internet Explorer */
	filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=3);
}

@include media-breakpoint-down(md) {
	/* X-Small devices (portrait phones, less than 576px) */

	html section.hero,
	html.dark-mode section.hero {
		background-attachment: local;
		background-position: 65%;
	}
	html section.hero {
		background-image: url(../../assets/img/cover/cover-xxl.jpg) !important;
		/*background-image: url('https://dummyimage.com/576x324') !important;*/
	}

	html.dark-mode section.hero {
		background-image: url(../../assets/img/cover/cover-dark-xxl.jpg) !important;
	}

	nav[data-toggle='toc'] {
		margin-bottom: 42px;
		position: static;
	}

	nav[data-toggle='toc'] .nav .active .nav {
		display: none;
	}
}

@include media-breakpoint-up(md) {
	/*  Small devices (landscape phones, portrait tablets, 576px and up) */
	html section.hero {
		background-image: url(../../assets/img/cover/cover-xxl.jpg) !important;
		/*background-image: url('https://dummyimage.com/768x432') !important;*/
		background-attachment: local;
	}

	html.dark-mode section.hero {
		background-image: url(../../assets/img/cover/cover-dark-xxl.jpg) !important;
		background-attachment: local;
	}

	nav[data-toggle='toc'] {
		margin-bottom: 42px;
		position: static;
	}

	nav[data-toggle='toc'] .nav .active .nav {
		display: none;
	}
}

@include media-breakpoint-down(lg) {
	.display-2 {
		font-size: $font-size-base * 2.5;
	}
}

@include media-breakpoint-up(lg) {
	/*  Medium devices (landscape tablets, 768px and up) */
	html section.hero {
		background-image: url(../../assets/img/cover/cover-xxl.jpg) !important;
		/*background-image: url('https://dummyimage.com/992x558') !important;*/
	}

	html.dark-mode section.hero {
		background-image: url(../../assets/img/cover/cover-dark-xxl.jpg) !important;
	}
}

@include media-breakpoint-up(xl) {
	/*  Large devices (desktops, 992px and up) */
	html section.hero {
		background-image: url(../../assets/img/cover/cover-xxl.jpg) !important;
		/*background-image: url('https://dummyimage.com/1200x675') !important;*/
	}

	html.dark-mode section.hero {
		background-image: url(../../assets/img/cover/cover-dark-xxl.jpg) !important;
	}
}

@include media-breakpoint-up(xxl) {
	/*  X-Large devices (large desktops, 1200px and up) */
	html section.hero {
		background-image: url(../../assets/img/cover/cover-xxl.jpg) !important;
		/*background-image: url('https://dummyimage.com/1400x16:9') !important;*/
	}

	html.dark-mode section.hero {
		background-image: url(../../assets/img/cover/cover-dark-xxl.jpg) !important;
	}
}

@media print {
	.d-print-none {
		display: none !important;
	}
	.d-print-only {
		display: block !important;
	}
	.container {
		width: auto;
	}
}
